button.bp3-button.bp3-minimal.bp3-outlined.bp3-intent-warning {
    padding-left: 12px;
    padding-right: 12px;
    cursor: pointer;
    height: 36px;
    display: flex;
    outline: none;
    background: #e3e3e3;
    align-items: center;
    line-height: 36px;
    border-radius: 18px !important;
}

.bp3-button.bp3-outlined.bp3-intent-warning:hover,.bp3-button.bp3-outlined.bp3-intent-warning:active, .bp3-button.bp3-outlined.bp3-intent-warning.bp3-active {
    background: rgb(65 43 217 / 15%);
    color: #7533E2 !important;
}

:focus {
    outline: rgb(52 19 189 / 60%) auto 2px;
    outline-offset: 2px;
}

.jss29 {
width: 100%;
border: 1px solid #c7cbd9;
display: flex;
padding: 10px;
margin-top: 20px;
border-radius: 6px;
background-color: white;
}

.jss29 img {
    width: 60px;
    border: 1px solid #c7cbd9;
    height: 60px;
    object-fit: cover;
    margin-right: 20px;
    border-radius: 5px;
}

.jss29 p span:first-child {
    font-size: 15px;
    font-weight: bold;
    margin-bottom: 3px;
}

.jss29 p {
    margin: 0;
    display: flex;
    word-break: break-all;
    line-height: normal;
    flex-direction: column;
}

g#_x31_3_Support_Ticket {
    color: #7b78ff;
}

.button-connected {
    border-style: solid;
    border-width: 1px;
    border-color: #bdc3c7;
    background-color: #f9f9fe;
    border-radius: 0.375rem;
    height: 38px;
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;
}

.bp3-html-select .bp3-icon, .bp3-select .bp3-icon, .bp3-select::after {
    top: 10px;
    right: 5px;
}

.bp3-button:not([class*="bp3-intent-"]) {
    height: 38px;
}

.bp3-html-select.bp3-minimal select, .bp3-select.bp3-minimal select {
    height: 38px;
}

.jss31:disabled {
    color: white !important;
    background-color: #3E3B51 !important;
}

.MuiButton-label {
    width: 100%;
    display: inherit;
    align-items: inherit;
    justify-content: inherit;
}

.jss31 {
    width: 50%;
    border: 0;
    cursor: pointer;
    height: calc(50px - 12px);
    outline: 0;
    font-weight: 600;
    border-radius: 5px;
}

.MuiButton-root {
    color: rgba(0, 0, 0, 0.87);
    padding: 6px 16px;
    font-size: 0.875rem;
    min-width: 64px;
    box-sizing: border-box;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    font-family: "Nunito Sans",sans-serif;
    font-weight: 500;
    line-height: 1.75;
    border-radius: 4px;
    text-transform: uppercase;
}

.wallet-option {
